/*
* Off Canvas
* --------------------------------------------------
*/
@media screen and (max-width: 767px) {
  .row-offcanvas {
    left: 0;
    position: relative;
    -webkit-transition: all .25s ease-out;
    -o-transition: all .25s ease-out;
    transition: all .25s ease-out;
  }
  .row-offcanvas .sidenav-offcanvas {
    left: -50vw;
    /* 6 columns */
  }
  .row-offcanvas.active {
    left: 50vw;
    /* 6 columns */
  }
  .sidenav-offcanvas {
    position: absolute;
    top: 0;
    width: 50vw;
    /* 6 columns */
  }
}

.topmost {
  z-index: 300;
  overflow: hidden;
  right: -12px;
  opacity: 0.4;
}

.topmost:hover {
  opacity: 0.9;
  right: 1px;
}

#scrollnav {
  -webkit-transition: top 0.5s, -webkit-transform 0.5s;
  transition: top 0.5s, transform 0.5s;
}

#scrollnav .nav > li > a {
  display: block;
  padding: 4px 20px;
  padding: 0.4rem 2rem;
  font-size: 14px;
  font-size: 1.4rem;
  color: #1e6887;
  text-decoration: underline;
}

#scrollnav .nav > li > a:focus,
#scrollnav .nav > li > a:hover {
  color: #4d4d4d;
  background-color: #F2F9FA;
  text-decoration: underline;
}

#scrollnav .nav > .active:focus > a,
#scrollnav .nav > .active:hover > a,
#scrollnav .nav > .active > a {
  color: #4d4d4d;
  background-color: #F2F9FA;
  text-decoration: none;
}

#scrollnav .nav .nav {
  display: none;
  padding-bottom: 2px;
  padding-bottom: 0.2rem;
}

#scrollnav .nav > li.active > .nav {
  display: block;
}

#scrollnav .nav .nav > li > a {
  padding-top: 1px;
  padding-top: 0.1rem;
  padding-bottom: 1px;
  padding-bottom: 0.1rem;
  padding-left: 30px;
